import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { Subject } from "rxjs";
import Swal from 'sweetalert2';

@Component({
  selector: "npr-authorize-cash-payment",
  templateUrl: "./authorize-cash-payment.component.html",
  styleUrls: ["./authorize-cash-payment.component.scss"],
})
export class AuthorizeCashPaymentComponent implements OnInit {
  search: boolean;
  respArray: any[] = [];
  taxInfoForm: FormGroup;
  taxresp: any;
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject<any>();

  constructor(private fb: FormBuilder) {}

  ngOnInit(): void {
    this.dtOptions = {
      pagingType: "full_numbers",
      pageLength: 5,
      processing: true, 
      // lengthMenu: [[5, 10, 20, -1], [5, 10, 20, 30]]
      lengthMenu: [
        [5, 10, 20, 30,-1],
        [5, 10, 20, 30,"ALL"],
      ],
    };
    this.buldForm("");
  }

  onSearchCall(prn) {
    if (prn != "5678910") {
      this.search = false;
      Swal.fire("Please Enter Valid PRN/E-slip No.");
    } else {
      this.search = true;
      this.respArray = [
        {
          eSlipNo: "5678910",
          pinNo: "4556675",
          amount: "3,60,000",
          debitAccount: "123456789",
        },
        {
          eSlipNo: "5678910",
          pinNo: "4556675",
          amount: "3,60,000",
          debitAccount: "123456789",
        },
        {
          eSlipNo: "5678910",
          pinNo: "4556675",
          amount: "3,60,000",
          debitAccount: "123456789",
        },
        {
          eSlipNo: "5678910",
          pinNo: "4556675",
          amount: "3,60,000",
          debitAccount: "123456789",
        },
      ];
    }
    this.taxresp = {
      accountStatus: "Process",
      amountExpected: "5467",
      branch: "Edavannapara",
      chequeNumber: "chq34567788",
      creditAccountNo: "679087689",
      currency: "Rupees",
      debitAccountNo: "123456789",
      inputter: "Vikas",
      paidBy: "Jayalakshmi PM",
      paymentType: "Accont",
      riskAuthorizer: "Fayaz",
      taxPayersName: "Joe di",
    };
    this.buldForm(this.taxresp);
  }
  buldForm(item) {
    this.taxInfoForm = this.fb.group({
      taxPayersName: [item ? item.taxPayersName : "", [Validators.required]],
      inputter: [item ? item.inputter : "", [Validators.required]],
      paymentType: [item ? item.paymentType : "", [Validators.required]],
      paidBy: [item ? item.paidBy : "", [Validators.required]],
      riskAuthorizer: [item ? item.riskAuthorizer : "", [Validators.required]],
      branch: [item ? item.branch : "", [Validators.required]],
      debitAccountNo: [item ? item.debitAccountNo : "", [Validators.required]],
      creditAccountNo: [
        item ? item.creditAccountNo : "",
        [Validators.required],
      ],
      chequeNumber: [item ? item.chequeNumber : "", [Validators.required]],
      amountExpected: [item ? item.amountExpected : "", [Validators.required]],
      currency: [item ? item.currency : "", [Validators.required]],
      accountStatus: [item ? item.accountStatus : "", [Validators.required]],
    });
  }
  save() {
    console.log(this.taxInfoForm);
  }
  ngOnDestroy(): void {
    this.dtTrigger.unsubscribe();
  }
  onReject()
  {
    Swal.fire(
      {
        title:"Payment Rejected"
      }
    )
  }
  onRework()
  {
    Swal.fire(
      {
        title:"Payment For Rework"
      }
    )
  }
  onApprove()
  {
    Swal.fire(
      {
        title:"Payment Approved"
      }
    )
  }
}
