import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthorizeCashPaymentComponent } from './authorize-cash-payment.component';

const routes: Routes = [
  {
    path:"",
    component:AuthorizeCashPaymentComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AuthorizeCashPaymentsRoutingModule { }
